const handleCreatePost = async (e) => {
  e.preventDefault();
  const token = localStorage.getItem("token");

  if (!token) {
    alert("Please login first to create a post!");
    router.push("/login");
    return;
  }

  if (!title || !description || !price) {
    setError("Please fill all required fields.");
    return;
  }

  try {
    const formData = new FormData();
    formData.append("title", title);
    formData.append("description", description);
    formData.append("price", price);
    formData.append("category", category);
    formData.append("status", "pending"); // pending post
    if (image) formData.append("image", image);
    if (video) formData.append("video", video);
    if (phone) formData.append("phone", phone);

    // üîπ ‡¶è‡¶á ‡¶≤‡¶æ‡¶á‡¶®‡¶ü‡¶æ ‡¶è‡¶ñ‡¶æ‡¶®‡ßá ‡¶¨‡¶∏‡¶æ‡¶§‡ßá ‡¶π‡¶¨‡ßá
    await api.post("/posts/create", formData, {
      headers: {
        "Content-Type": "multipart/form-data",
        Authorization: `Bearer ${token}`,
      },
    });

    setSuccess("‚úÖ Post created successfully! Waiting for admin approval.");
    setError("");
    // Reset form
    setTitle("");
    setDescription("");
    setPrice("");
    setCategory("Gaming");
    setImage(null);
    setVideo(null);
    setPhone("");
  } catch (err) {
    console.error(err);
    setError("‚ùå Failed to create post. Try again.");
    setSuccess("");
  }
};
